        // 
        function SelectAll(chk,index) {
            var chk = document.getElementsByName(chk);
            var obj = chk[0];
            if(index != undefined && index !=''){
                obj = chk[chk.length-1];
            }
            if(obj.checked){
                for ( var i = 0; i < chk.length; i++) {
                    chk[i].checked = true;
                }
            }else{
                for ( var i = 0; i < chk.length; i++) {
                    chk[i].checked =    false;
                }
            }
        }


