(function() {
    'use strict';

    angular
        .module('EBankProject')
        .filter('intercept', intercept);
    /**
    *   
    */
    function intercept() {
        return function(input) {
            var inputFile = input;
            var reg = /\[(.*?)\]/gi;
            var tmp = inputFile.match(reg);
            if (tmp) {
                for (var i = 0; i < tmp.length; i++) {
                    // alert(tmp[i]); // 
                    // alert(tmp[i].replace(reg, "$1")); // 
                    inputFile = tmp[i].replace(reg, "$1");
                }
            }
            return inputFile;
        };
    }
})();

