(function() {
    'use strict';

    angular
        .module('EBankProject')
        .filter('ChinaCost', ChinaCost);

    /**
     * 
     * @memberof EBankProject
     * @function ChinaCost
     * example:
     * <div ng-bind="'1000'|ChinaCost}"></div>
     * @description 
     * input  
     */

    function ChinaCost() {
         return  function(input){
          if(input){
           var numberValue=new String(Math.round(input*100)); // 
        var chineseValue=""; // 
        var String1 = ""; // 
        var String2 = ""; // 
        var len=numberValue.length; // numberValue 
        var Ch1; // 
        var Ch2; // 
        var nZero=0; // 
        var String3; // 
        if(len>15){
        alert("");
        return "";
        }
        if (numberValue==0){
        chineseValue = "";
        return chineseValue;
        }

        String2 = String2.substr(String2.length-len, len); // STRING2
        for(var i=0; i<len; i++){
        String3 = parseInt(numberValue.substr(i, 1),10); // 
        if ( i != (len - 3) && i != (len - 7) && i != (len - 11) && i !=(len - 15) ){
        if ( String3 == 0 ){
        Ch1 = "";
        Ch2 = "";
        nZero = nZero + 1;
        }
        else if ( String3 != 0 && nZero != 0 ){
        Ch1 = "" + String1.substr(String3, 1);
        Ch2 = String2.substr(i, 1);
        nZero = 0;
        }
        else{
        Ch1 = String1.substr(String3, 1);
        Ch2 = String2.substr(i, 1);
        nZero = 0;
        }
        }
        else{ // 
        if( String3 != 0 && nZero != 0 ){
        Ch1 = "" + String1.substr(String3, 1);
        Ch2 = String2.substr(i, 1);
        nZero = 0;
        }
        else if ( String3 != 0 && nZero == 0 ){
        Ch1 = String1.substr(String3, 1);
        Ch2 = String2.substr(i, 1);
        nZero = 0;
        }
        else if( String3 == 0 && nZero >= 3 ){
        Ch1 = "";
        Ch2 = "";
        nZero = nZero + 1;
        }
        else{
        Ch1 = "";
        Ch2 = String2.substr(i, 1);
        nZero = nZero + 1;
        }
        if( i == (len - 11) || i == (len - 3)){ // 
        Ch2 = String2.substr(i, 1);
        }
        }
        chineseValue = chineseValue + Ch1 + Ch2;
        }

        if ( String3 == 0 ){ // 0
        chineseValue = chineseValue + "";
        }

        return chineseValue;
          }
    };
    }
})();




